* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen-Sans, Ubuntu, Cantarell,
    'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

img {
  display: block;
  max-width: 100%;
}

button {
  min-width: 70px;
  height: 30px;
  cursor: pointer;
}

.timer {
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  font-size: 40px;
}

.field {
  margin-right: 50px;
}

.field:last-child {
  margin-right: 0;
}

.label {
  display: block;
  font-weight: 500;
}
